@keyframes loading { 
    50% {
    
    }
    100% {
    transform:rotate(360deg); 
    }}
    .loader-container{
        display:grid;
        place-items:center;
        width:100vw;
        height:100vh;
        position:fixed;
        z-index: 1000000;
        background-color: white;
    }
    .loading-text{
        transform:translate(0,50px);
        font-size:1.3rem;
        font-family:'hk'
    }
  #loading {
      margin: auto;
      position: absolute;
      top: 0; left: 0; bottom: 0; right: 0;
      animation: loading 4s linear infinite;
      width: 40px;
      height: 40px;
      opacity: 0.8;
    //   background: url(data:image/svg+xml;base64,);  
    background-size:contain;
  }
  
  